}*/

input:invalid {
  background-color: #ffdddd;
}


/* ======= SIDEBAR ====== */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 20px;
  top: 55px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  /*background: #373a47;*/
  background: #505359;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

.bm-menu-wrap {
	/*overflow-y:hidden;*/
}

/* General sidebar styles */
.bm-menu {
  background: #505359;
  padding: 1.5em 1em 0;
  font-size: 1.15em;
  overflow:initial!important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #505359;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0 0.75em 0 0.8em;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


